import React, {useState} from 'react';
import {makeStyles} from '@material-ui/core/styles';
import OrderImage from './OderImage';
import SelectCustom from '../../../components/common/SelectCustom';
import TextFieldLite from '../../../components/common/TextFieldLite';
import PurpleCheckbox from '../../../components/common/PurpleCheckbox';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import PlusRoundButton from '../../../components/common/PlusRoundButton';
import IconButton from '@material-ui/core/IconButton';

const MakeOrderMakeOrderCreated = () => {
  const classes = useStyles();
  return (
      <div className={classes.makeOrderCreatedBlock}>
        <div className={classes.makeOrderCreatedBlockLineOne}>
          <OrderImage width='200px' height='200px'/>
        </div>
      </div>
  );
};

const useStyles = makeStyles((theme) => ({
  makeOrderCreatedBlock: {
    border: '1px solid #a7a0a0',
    boxShadow: '0px 10px 15px -5px rgba(0,0,0,0.5)',
    padding: '3%',
    paddingTop: '0',
    flexWrap: 'wrap',
    height: 'fit-content',
    marginTop: '20px',
    ['@media (max-width:1300px)']: {
      flexBasis: '100%',
      maxWidth: 'initial',
      marginBottom: '20px',
    },
    ['@media (max-width:668px)']: {
      border: 'none',
      boxShadow: 'none',
      margin: '0 5px',
    },
  },
  makeOrderCreatedBlockLineOne: {
    display: 'flex',
    marginTop: '10px',
    borderBottom: '1px solid #c4bdbd',
    paddingBottom: '20px',
  },
}));

export default MakeOrderMakeOrderCreated;
